*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.content {
    position: relative;
    width: 100%;
    height: 100vh;
    /* background: #111; */
    color: #fff;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url(assets/bg-2.jpg);
}

a{
    text-decoration: none;
    color: #fff;
}

ul{
    list-style: none;
}

header{
    position: absolute;
    top: 1rem;
    left: 60%;
}

.nav_list{
    display: flex;
    gap: 2rem;
}

li{
    font-size: 20px;
    font-weight: 700;
}

.description{
    position: absolute;
    top: 30%;
    left: 5rem;
    width: 100%;
    height: 60%;
    color: #fff;
}

h1{
    font-size: 80px;
}

h2{
    font-size: 55px;
    width: 93%;
    padding-top: 1rem;
}